// 
//  routerConfig 
//  routerConfig  iceworks 

import Dashboard from 'pages/Dashboard';
import Notifications from 'pages/Notifications';
import Icons from 'pages/Icons';
import TableList from 'pages/TableList';
import Upgrade from 'pages/Upgrade';
import UserPage from 'pages/UserPage';

const dashRoutes = [
  {
    path: '/dashboard',
    name: 'Dashboard',
    component: Dashboard,
  },
  {
    path: '/icons',
    name: 'Icons',
    component: Icons,
  },
  {
    path: '/notifications',
    name: 'Notifications',
    component: Notifications,
  },
  {
    path: '/user-page',
    name: 'User Profile',
    component: UserPage,
  },
  {
    path: '/extended-tables',
    name: 'Table List',
    component: TableList,
  },
  {
    pro: true,
    path: '/upgrade',
    name: 'Upgrade to PRO',
    component: Upgrade,
  },
  {
    redirect: true,
    path: '/',
    pathTo: '/dashboard',
    name: 'Dashboard',
  },
];

export default dashRoutes;



